import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingDepartmentWrapper from "../OneOngoingDepartmentWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import DepartmentCancelledCard from "./DepartmentCancelledCard";
import OneOngoingDepartmentSearchWrapper from "../OneOngoingDepartmentSearchWrapper";
import { useDepartmentOngoingApplicationCancelled } from "../../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
const DepartmentCancelled = ({
  applicationDetail,
  instituteId,
  urlOffSet,
  departmentId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    departmentOngoingApplicationCancelled,
    departmentOngoingApplicationCancelledLoading,
  } = useDepartmentOngoingApplicationCancelled({
    data: {
      appId: getQuery.state?.appId,
      did: departmentId,

      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId ? !getQuery.state?.appId : !departmentId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (departmentOngoingApplicationCancelled?.list?.length) {
      let obj = nextPage(departmentOngoingApplicationCancelled?.list?.length);
      setState(() => obj);
    }
  }, [departmentOngoingApplicationCancelled?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !departmentOngoingApplicationCancelledLoading &&
      departmentOngoingApplicationCancelled?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingDepartmentWrapper
          applicationDetail={applicationDetail}
          urlOffSet={urlOffSet}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingDepartmentSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            isFull
          >
            <input
              type="text"
              placeholder={t("search_cancelled_student")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingDepartmentSearchWrapper>

          {departmentOngoingApplicationCancelled?.list?.map(
            (applicant, index) =>
              departmentOngoingApplicationCancelled?.list?.length ===
              index + 1 ? (
                <div key={applicant?._id} ref={ref}>
                  <DepartmentCancelledCard
                    applicant={applicant}
                    instituteId={instituteId}
                  />
                </div>
              ) : (
                <DepartmentCancelledCard
                  applicant={applicant}
                  key={applicant?._id}
                  instituteId={instituteId}
                />
              )
          )}
          {departmentOngoingApplicationCancelledLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            departmentOngoingApplicationCancelled?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission cancelled found related this search."
              />
            )
          ) : (
            departmentOngoingApplicationCancelled?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission cancelled list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default DepartmentCancelled;
