import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingDepartmentWrapper from "../OneOngoingDepartmentWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import DepartmentReviewCard from "./DepartmentReviewCard";
import OneOngoingDepartmentSearchWrapper from "../OneOngoingDepartmentSearchWrapper";
import AdmissionReviewResuffleMenu from "../../../AdmissionManager/OneOngoingAdmission/AdmissionReview/AdmissionReviewResuffleMenu";
import { useDepartmentOngoingApplicationReview } from "../../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";

const DepartmentReview = ({
  applicationDetail,
  instituteId,
  aid,
  urlOffSet,
  departmentId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [resuffle, setResuffle] = useState(false);

  const {
    departmentOngoingApplicationReview,
    departmentOngoingApplicationReviewLoading,
    departmentOngoingApplicationReviewRefetch,
  } = useDepartmentOngoingApplicationReview({
    data: {
      appId: getQuery.state?.appId,
      did: departmentId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId ? !getQuery.state?.appId : !departmentId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (departmentOngoingApplicationReview?.list?.length) {
      let obj = nextPage(departmentOngoingApplicationReview?.list?.length);
      setState(() => obj);
    }
  }, [departmentOngoingApplicationReview?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !departmentOngoingApplicationReviewLoading &&
      departmentOngoingApplicationReview?.list?.length >= 0
    ) {
      // if (e.target.value) {
      setSearchCopy(e.target.value);
      // } else {
      //   setSearchCopy("");
      //   setSearch("");
      // }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    departmentOngoingApplicationReviewRefetch();
  }, [departmentOngoingApplicationReviewRefetch]);

  const onMultipleSelect = () => {
    let url =
      urlOffSet === "NORMAL"
        ? `/q/${params.username}/member/department/${params.deptname}?a=ongoing-detail&n=review-multi`
        : `/q/${params.username}/member/staff/academic/department/${params.name}?type=ongoing-detail&n=review-multi`;
    navigate(url, { state: getQuery.state });
  };

  const onResuffleToggle = useCallback(() => {
    setResuffle((pre) => !pre);
  }, []);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingDepartmentWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
          // isTransferAccess="Review"
          aid={aid}
          urlOffSet={urlOffSet}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingDepartmentSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            isExportAccess="Review_Query"
            // isTransferAccess="Review"
            aid={aid}
            onMultipleSelect={onMultipleSelect}
            onResuffleToggle={onResuffleToggle}
          >
            <input
              type="text"
              placeholder={t("search_applicants")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingDepartmentSearchWrapper>

          {departmentOngoingApplicationReview?.list?.map((applicant, index) =>
            departmentOngoingApplicationReview?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <DepartmentReviewCard
                  applicant={{ student: applicant }}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                  appId={getQuery.state?.appId}
                />
              </div>
            ) : (
              <DepartmentReviewCard
                applicant={{ student: applicant }}
                key={applicant?._id}
                instituteId={instituteId}
                onRefetch={onRefetch}
                appId={getQuery.state?.appId}
              />
            )
          )}
          {departmentOngoingApplicationReviewLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            departmentOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants found related this search."
              />
            )
          ) : (
            departmentOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {resuffle && (
        <AdmissionReviewResuffleMenu
          onClose={onResuffleToggle}
          appId={applicationDetail?._id}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentReview;
const isAuthorizedComplete = {
  isCompleted: true,
};
