import { useLocation, useSearchParams } from "react-router-dom";
import { useAdmissionOneOngoingApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import DepartmentApplication from "./DepartmentApplication/DepartmentApplication";
import DepartmentAllottment from "./DepartmentAllottment/DepartmentAllottment";
import DepartmentCancelled from "./DepartmentCancelled/DepartmentCancelled";
import DepartmentDocumentCollection from "./DepartmentDocumentCollection/DepartmentDocumentCollection";
import DepartmentFeesCollection from "./DepartmentFeesCollection/DepartmentFeesCollection";
import DepartmentMultipleConfirm from "./DepartmentConfirm/DepartmentMultipleConfirm";
import DepartmentConfirm from "./DepartmentConfirm/DepartmentConfirm";
import DepartmentMultipleReview from "./DepartmentReview/DepartmentMultipleReview";
import DepartmentReview from "./DepartmentReview/DepartmentReview";

const OneOngoingDepartmentConfig = ({
  instituteId,
  admissionId,
  financeId,
  urlOffSet,
  departmentId,
}) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { admissionOneOngoingApplication } = useAdmissionOneOngoingApplication({
    appId: getQuery.state?.appId,
    skip: !getQuery.state?.appId,
  });
  // console.info(
  //   "admissionOneOngoingApplication",
  //   admissionOneOngoingApplication
  // );
  return (
    <>
      {!searchParams.get("n") ? (
        <DepartmentApplication
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={admissionId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "document" ? (
        <DepartmentDocumentCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={admissionId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "fees" ? (
        <DepartmentFeesCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={admissionId}
          financeId={financeId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "confirmed" ? (
        <DepartmentConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "confirmed-multi" ? (
        <DepartmentMultipleConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "review" ? (
        <DepartmentReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={admissionId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "review-multi" ? (
        <DepartmentMultipleReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={admissionId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "allottment" ? (
        <DepartmentAllottment
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : searchParams.get("n") === "cancelled" ? (
        <DepartmentCancelled
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          urlOffSet={urlOffSet}
          departmentId={departmentId}
        />
      ) : null}
    </>
  );
};

export default OneOngoingDepartmentConfig;
