import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import DepartmentApplicationCard from "./DepartmentApplicationCard";
import OneOngoingDepartmentSearchWrapper from "../OneOngoingDepartmentSearchWrapper";
import OneOngoingDepartmentWrapper from "../OneOngoingDepartmentWrapper";
import { useDepartmentOngoingApplicationRequest } from "../../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
const DepartmentApplication = ({
  applicationDetail,
  instituteId,
  aid,
  urlOffSet,
  departmentId,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    departmentOngoingApplicationRequest,
    departmentOngoingApplicationRequestLoading,
    departmentOngoingApplicationRequestRefetch,
  } = useDepartmentOngoingApplicationRequest({
    data: {
      appId: getQuery.state?.appId,
      did: departmentId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId ? !getQuery.state?.appId : !departmentId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (departmentOngoingApplicationRequest?.list?.length) {
      let obj = nextPage(departmentOngoingApplicationRequest?.list?.length);
      setState(() => obj);
    }
  }, [departmentOngoingApplicationRequest?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !departmentOngoingApplicationRequestLoading &&
      departmentOngoingApplicationRequest?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    departmentOngoingApplicationRequestRefetch();
  }, []);
  // console.info("applicationDetail", applicationDetail);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingDepartmentWrapper
          applicationDetail={applicationDetail}
          urlOffSet={urlOffSet}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingDepartmentSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            // isAddAccess="APPLICATION"
            isExportAccess="Request_Query"
            // aid={aid}
            // isGenerateAccess="Request_Query"
          >
            <input
              type="text"
              placeholder={t("search_applicants")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingDepartmentSearchWrapper>

          {departmentOngoingApplicationRequest?.list?.map((applicant, index) =>
            departmentOngoingApplicationRequest?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <DepartmentApplicationCard
                  applicant={applicant}
                  instituteId={instituteId}
                  did={applicationDetail?.applicationDepartment?._id}
                  bid={applicationDetail?.applicationBatch?._id}
                  cmid={applicationDetail?.applicationMaster?._id}
                  onRefetch={onRefetch}
                  appId={getQuery.state?.appId}
                  admissionIntake={applicationDetail?.admission_intake}
                />
              </div>
            ) : (
              <DepartmentApplicationCard
                key={applicant?._id}
                applicant={applicant}
                instituteId={instituteId}
                did={applicationDetail?.applicationDepartment?._id}
                bid={applicationDetail?.applicationBatch?._id}
                cmid={applicationDetail?.applicationMaster?._id}
                onRefetch={onRefetch}
                appId={getQuery.state?.appId}
                admissionIntake={applicationDetail?.admission_intake}
              />
            )
          )}
          {departmentOngoingApplicationRequestLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            departmentOngoingApplicationRequest?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission requests found related this search."
              />
            )
          ) : (
            departmentOngoingApplicationRequest?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission request list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default DepartmentApplication;
