import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import {
  assestsUserAdmissionUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useCallback, useState } from "react";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import ApplicationSubjectList from "../../AdmissionManager/OneOngoingAdmission/ApplicationSubjectList/ApplicationSubjectList";
import OneOngoingAdmissionDetail from "../../AdmissionManager/OneOngoingAdmission/OneOngoingAdmissionDetail";

const OneOngoingDepartmentWrapper = ({
  applicationDetail,
  urlOffSet,
  children,
}) => {
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");

  const onCloseStep = () => {
    setStep("");
  };
  const onApplicationDetail = () => {
    setStep(1);
  };
  const onNavigate = useCallback(() => {
    let url = "";
    url =
      urlOffSet === "NORMAL"
        ? `/q/${params.username}/member/department/${params.deptname}?a=ongoing`
        : `/q/${params.username}/member/staff/academic/department/${params.name}?type=ongoing`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);
  // console.info("sd,fbfsn", step);

  const onSubjectList = () => {
    setStep(2);
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{getQuery.state?.applicationName ?? ""}</h6>
          </div>

          <div className={style.fm_search}>
            <img
              src={`${assestsUserAdmissionUrl}/application-detail.svg`}
              alt="icon"
              title="Application details"
              onClick={onApplicationDetail}
            />
            <CreateButton
              label="subject_count"
              onAction={onSubjectList}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
              }}
            />
            {children}
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              urlOffSet === "NORMAL"
                ? `/q/${params.username}/member/department/${params.deptname}?a=ongoing-detail${tab.urlKey}`
                : `/q/${params.username}/member/staff/academic/department/${params.name}?type=ongoing-detail${tab.urlKey}`
            }
            extraData={
              getQuery.state?.applicationData && {
                count: getQuery.state?.applicationData[tab?.id],
              }
            }
          />
        ))}
      </HorizontalTabWrapper>

      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      {step === 1 ? (
        <OneOngoingAdmissionDetail
          applicationDetail={applicationDetail}
          onClose={onCloseStep}
          isDepartment
        />
      ) : step === 2 ? (
        <ApplicationSubjectList
          appId={applicationDetail?._id}
          onClose={onCloseStep}
          isDepartment
          parentUrl={
            urlOffSet === "NORMAL"
              ? `/q/${params.username}/member/department/${params.deptname}?a=selected-subject`
              : `/q/${params.username}/member/staff/academic/department/${params.name}?type=selected-subject`
          }
        />
      ) : null}
    </>
  );
};

export default OneOngoingDepartmentWrapper;

const tabList = [
  {
    label: "application",
    id: "application",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "docs_collection",
    id: "document",
    urlKey: "&n=document",
    groupIs: ["document"],
  },
  {
    label: "fees_collection",
    id: "fees",
    urlKey: "&n=fees",
    groupIs: ["fees"],
  },
  {
    label: "confirmed",
    id: "confirmed",
    urlKey: "&n=confirmed",
    groupIs: ["confirmed", "confirmed-multi"],
  },
  {
    label: "review",
    id: "review",
    urlKey: "&n=review",
    groupIs: ["review", "review-multi"],
  },
  {
    label: "allottment",
    id: "allottment",
    urlKey: "&n=allottment",
    groupIs: ["allottment"],
  },
  {
    label: "cancelled",
    id: "cancelled",
    urlKey: "&n=cancelled",
    groupIs: ["cancelled"],
  },
];
